* {
  scroll-behavior: smooth;
  margin: 0;
}

.banner {
  background-image: url("../../Images/car1.png.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vmin;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: aliceblue;
}
.banner h1 {
  margin: 5vmax;
  font: 900 3.4vmax "Roboto";
  margin-top: 1.9vmax;
}

.banner p {
  font: 300 2.1vmax "Lucida Sans";
}

.banner a button {
  margin-bottom: 5vmax;
  cursor: pointer;
  background-color: aliceblue;
  border: 1px solid white;
  border-radius: 0.5vmax;
  padding: 1vmax;
  transition: all 0.5s;
  width: 9vmax;
  font: 500 1vmax "Roboto";
}

.banner a button:hover {
  background-color: rgba(255, 255, 255, 0);
  color: white;
}

.banner::after {
  content: "";
  width: 100vw;
  height: 100vmin;
  background-color: aliceblue;
  position: absolute;
  top: 0%;
  left: 0;
  clip-path: polygon(100% 82%, 0 100%, 100% 100%);
  max-width: 100%;
}

.homeHeading {
  /* padding-top: 0px;
  margin-top: 0px; */
  text-align: center;
  font-family: Roboto;
  font-size: 1.4vmax;
  border-bottom: 1px solid rgba(21, 21, 21, 0.5);
  width: 20vmax;
  padding: 1vmax;
  margin: 5vmax auto;
  margin-top: 10px;
  margin-bottom: 10px;
  color: rgb(0, 0, 0, 0.7);
}

.containerrr {
  display: flex;
  margin: 2vmax auto;
  width: 80vw;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 100%;
}

.productCard {
  width: 14vmax;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 1.5vmax;
  transition: all 0.5vmax;
  padding-bottom: 0.5vmax;
}

.productCard img {
  width: 14vmax;
}

.productCard p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard div {
  margin: 0.02vmax;
  /* margin-top: 0px; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* font-size: 35px; */
}

.productCardSpan {
  margin: 0.7vmax;
  /* margin-top: 0px; */
  font: 300 0.7vmax "Roboto";
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.3vmax;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.5);
  transform: translateY(-1vmax);
}

@media screen and (max-width: 600px) {
  .productCard > p {
    font-size: 1.7vmax;
  }
  .productCard > div {
    margin: 0vmax;
    display: block;
  }
  .productCard > span {
    font-size: 1.5vmax;
  }
  .productCard > div > span {
    margin: 0 0.5vmax;
    font: 300 1vmax "Roboto";
  }
}

.title {
  font-size: 50px;
  text-align: center;
  margin-top: 80px;
  margin-bottom: 40px;
  text-decoration: none;
  font: 500 2.99vmax Roboto;
  border-bottom: 2px;

  text-align: center;
  font-family: Roboto;
  font-size: 1.6vmax;
  border-bottom: 1px solid rgba(21, 21, 21, 0.604);
  width: 20vmax;
  padding: 1vmax;
  margin: 3vmax auto;
  margin-top: 25px;
  margin-bottom: 19px;
  color: rgb(0, 0, 0, 0.7);
}

.questions-container {
  max-width: 800px;
  margin: 0 auto;
}

.question {
  border-bottom: 1px solid #cecbcbbd;
}
.question button {
  width: 100%;
  background-color: #ece7e7a8;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 15px;
  border: none;
  outline: none;
  font-size: 22px;
  color: #1b1a1aa1;
  font-weight: 700;
  cursor: pointer;
}
.question p {
  font-size: 22px;
  max-height: 0;
  opacity: 0;
  line-height: 1.5;
  overflow: hidden;
  transition: all 0.6s ease;
}
.d-arrow {
  transition: transform 0.5s ease-in;
  color: #fff;
}

/*add this class when click*/
.question p.show {
  max-height: 200px;
  opacity: 1;
  padding: 0px 15px 30px 15px;
}
.question button .d-arrow.rotate {
  transform: rotate(180deg);
}

/* Counter section   */
.wrapper {
  /* position: absolute; */
  /* width: 80vw; */
  /* transform: translate(-50%, -50%); */
  /* top: 50%;
  left: 50%; */
  padding-top: 30px;
  display: flex;
  justify-content: space-evenly;
  gap: 1px;
  width: 75%;
  margin: auto;
}
.container {
  width: 28vmin;
  height: 25vmin;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1em 0;
  position: relative;
  font-size: 16px;
  border-radius: 0.5em;
  /* background-color: #eceef4; */
  border-bottom: 10px solid #868d8a66;
  border-top: 10px solid #868d8a66;
}

.i {
  color: #fe5b3d;
  background: #eeeff1;
  border-radius: 0.5rem;
  font-size: 40;
  text-align: center;
  justify-content: center;
  margin: auto;
  margin-top: 10px;
  /* size: 20px; */
  padding: 2px;
}

.i:hover {
  background: linear-gradient(#fe5d3d, #ffac38);
  color: #fff;
}
span.num {
  color: #363434;
  /* display: grid; */
  place-items: center;
  justify-content: center;
  margin: auto;
  font-weight: 600;
  font-size: 4em;
  margin-top: 0px;
}
span.text {
  color: #c43333;
  font-size: 1.5em;
  text-align: center;
  pad: 0.7em 0;
  font-weight: 400;
  line-height: 2;
  margin-top: 0px;
}
@media screen and (max-width: 1024px) {
  .wrapper {
    width: 85vw;
  }
  .container {
    height: 26vmin;
    width: 26vmin;
    font-size: 12px;
  }
}
@media screen and (max-width: 768px) {
  .wrapper {
    width: 90vw;
    flex-wrap: wrap;
    gap: 30px;
  }
  .container {
    width: calc(50% - 40px);
    height: 30vmin;
    font-size: 14px;
  }
}
@media screen and (max-width: 480px) {
  .wrapper {
    gap: 15px;
  }
  .container {
    width: 100%;
    height: 25vmin;
    font-size: 8px;
  }
}

/* Rental details section  */
.ride {
  padding: 25px;
  padding-top: 35px;
}
.heading {
  text-align: center;
}

.heading span {
  padding-top: 15px;
  /* font-weight: 500; */
  font: 600 2vmax "Roboto";
  /* text-transform: uppercase; */
}

.heading h1 {
  font-size: 2rem;
  font: 600 2vmax "Roboto";
}

.ride-container {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  gap: 1rem;
  margin-top: 2rem;
  width: 80%;
  margin: auto;
}

.ride-container .box {
  text-align: center;
  padding: 30px;
}
.ride-container .box .bx {
  font-size: 34px;
  padding: 10px;
  background: #eeeff1;
  border-radius: 0.5rem;
  color: #fe5b3d;
}

.ride-container .box h2 {
  /* font-size: 1.3rem; */
  /* font-weight: 800; */
  font: 600 1vmax "Roboto";
  margin: 1.4rem 0 0.5rem;
}

.ride-container .box p {
  /* font-size: 1.3rem; */
  /* font-weight: 800; */
  font: 600 1vmax "Roboto";
  margin: 1.4rem 0 0.5rem;
  padding-left: 25px;
  padding-right: 30px;
}

.ride-container .box .bx:hover,
.ride-container .box .bxs-calendar-star {
  background: linear-gradient(#fe5d3d, #ffac38);
  color: #fff;
}

/* CAR SECTION */

.ccard {
  display: inline-block;
  padding: 3px 6px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #171616;
  background-color: #ece4e400;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #99999900;
  /* margin: 0.1vmax; */
  margin-right: 20px;
  margin-bottom: 20px;
}
.ccard:hover {
  border: 0.5px solid rgba(133, 129, 129, 0.212);
}
.ccard:active {
  /* box-shadow: 0 5px #666;
  transform: translateY(4px); */
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.666);
  transform: translateY(-1vmax);
}
.ccard > img {
  /* border: 1px solid #ddd; */
  border-radius: 4px;
  padding: 5px;
  width: 260px;
}
.ccard > h3 {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.3vmax;
  text-align: left;
  padding-left: 15px;
}
.ccard > h1 {
  font-family: "Roboto";
  font-size: 1.5vmax;
  margin: 0vmax;
  margin-bottom: 0;
  text-align: left;
  padding-left: 15px;
}
.carContainer {
  width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin: auto;
  margin: 2vmax auto;
}

.carSelectHeading {
  text-align: center;
  font-family: Roboto;
  font-size: 1.6vmax;
  border-bottom: 1px solid rgba(21, 21, 21, 0.604);
  width: 20vmax;
  padding: 1vmax;
  margin: 5vmax auto;
  margin-top: 10px;
  margin-bottom: 10px;
  color: rgb(0, 0, 0, 0.7);
}
