.Tablesize {
  width: 100vw;
  max-width: 100%;
  padding: 0 7vmax;
  box-sizing: border-box;
  background-color: rgba(127, 255, 212, 0.603);
}
table {
  border-collapse: collapse;
  width: 80%;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding-top: 55px;
  margin-top: 100px;
  margin-bottom: 600px;
  padding-bottom: 1500px;
}

th,
td {
  border: 1px solid black;
  padding: 8px;
  text-align: left;
  border-radius: 2px;
  border-style: dashed;
  border-color: rgba(154, 225, 96, 0.803);
  font-size: 20px;
  font-family: "Roboto";
}

th {
  background-color: #f5e5e5;
  font-weight: 900;
}
