footer {
  padding: 2vmax;
  background-color: rgb(46, 46, 46);
  color: aliceblue;
  display: flex;
  align-items: center;
  /* width: 80%; */
  margin: auto;
  margin-top: 10vmax;
  padding-left: 3vmax;
  padding-right: 3vmax;
}
.footer1 {
  background-color: rgb(46, 46, 46);
  /* color: aqua; */
}

.leftFooter h1 {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-align: center;
  font-size: 2vmax;
}

.leftFooter > p {
  text-align: center;
  font-size: 1.2vmax;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.leftFooter img {
  width: 8vmax;
  margin: auto;
  cursor: pointer;
  display: block;
}

.midFooter {
  width: 40%;
  text-align: center;
}

.midFooter h1 {
  font-size: 3.5vmax;
  font-family: "Roboto";
  color: rgb(224, 220, 217);
}

.midFooter p {
  max-width: 60%;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin: 1vmax auto;
}

.rightFooter {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rightFooter h4 {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.7vmax;
  text-decoration: underline;
}

.rightFooter a {
  text-decoration: none;
  font-size: 1.5vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: aliceblue;
  transition: all 0.5s;
  margin: 0.5vmax;
}

.rightFooter a:hover {
  color: aquamarine;
}

.rightFooter1 h4 {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.5vmax;
  text-decoration: underline;
}

.rightFooter1 > p {
  text-decoration: none;
  font-size: 1.5vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: aliceblue;
  transition: all 0.5s;
  margin: 0.5vmax;
}

.rightFooter1 {
  padding-left: 45px;
}
