.productListContainer {
  width: 100%;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  border-left: 1px solid rgba(0, 0, 0, 0.158);
  display: flex;
  flex-direction: column;
  height: 100vh;
}

#productListHeading {
  font: 400 2rem "Roboto";
  padding: 0.5vmax;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.637);
  transition: all 0.5s;
  margin: 2rem;
  text-align: center;
}

.productListTable {
  background-color: rgb(255, 255, 255);
  border: none !important;
}

.productListTable div {
  font: 300 1vmax "Roboto";
  color: rgba(0, 0, 0, 0.733);
  border: none !important;
}

.productListTable a,
.productListTable button {
  color: rgba(2, 2, 2, 0.849);
  transition: all 0.5s;
}

.productListTable a:hover {
  color: tomato;
}

.productListTable button:hover {
  color: rgb(236, 30, 30);
}

.MuiDataGrid-columnHeader {
  background-color: rgba(235, 87, 87, 0.952);
  padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
  color: rgb(255, 255, 255);
  font: 300 1.1vmax "Roboto";
  /* background-color: rgba(241, 83, 83, 0.897); */
  border-left: none;
  border-right: none;
}

.MuiDataGrid-iconSeparator {
  display: none !important;
}
/* 
.MuiDataGrid-columnHeader div {
  color: rgb(219 47 47);
} */
@media screen and (max-width: 600px) {
  .productListTable div {
    font: 300 4vw "Roboto";
  }
}
