.shippingContainer {
  width: 100vw;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.shippingBox {
  background-color: white;
  width: 25vw;
  height: 90vh;
  box-sizing: border-box;
  overflow: hidden;
}

.shippingHeading {
  text-align: center;
  color: rgba(0, 0, 0, 0.664);
  font: 400 1.3vmax "Roboto";
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 50%;
  margin: auto;
}

.shippingForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 80%;
  transition: all 0.5s;
}

.shippingForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}

.shippingForm > div > input,
.shippingForm > div > select {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.pdate {
  font-size: 13px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 1px;
  /* line-height: 3em; */
  padding-left: 0.1em;
  padding-top: 1.5em;
  padding: 1vmax 1vmax;
  width: 50%;
  color: rgba(0, 0, 0, 0.4);
  border: none;
}
.shippingForm > div > input.dd {
  padding: 1vmax 1vmax;
  border: none;
}
.shippingForm > div > input.ddd {
  padding: 1vmax 1vmax;
  border: none;
}
.ddate {
  font-size: 13px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  /* line-height: 3em; */
  padding-left: 0.25em;
  padding-top: 1.5em;
  padding: 1vmax 1vmax;
  width: 50%;
  color: rgba(0, 0, 0, 0.4);
  border: none;
}
